import React, { useState, useRef, useCallback } from "react";
import {
  Box,
  Avatar,
  Menu,
  MenuItem,
  ListItemIcon,
  ListItemText,
  Divider,
  Typography,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  IconButton,
  Paper,
  CircularProgress,
  Snackbar,
  Alert,
} from "@mui/material";
import {
  Person,
  Edit,
  DeleteForever,
  Logout,
  Warning,
  AddAPhoto,
} from "@mui/icons-material";
import { useAuth } from "../contexts/AuthContext";

interface ProfileMenuProps {
  anchorEl: HTMLElement | null;
  onClose: () => void;
  onClearData: () => void;
}

const ProfileMenu: React.FC<ProfileMenuProps> = ({
  anchorEl,
  onClose,
  onClearData,
}) => {
  const { user, logout, updateProfile } = useAuth();
  const [editProfileOpen, setEditProfileOpen] = useState(false);
  const [resetConfirmOpen, setResetConfirmOpen] = useState(false);
  const [displayName, setDisplayName] = useState(user?.displayName || "");
  const [photoURL, setPhotoURL] = useState(user?.photoURL || "");
  const [isDragging, setIsDragging] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [uploadedFile, setUploadedFile] = useState<File | null>(null);
  const [previewUrl, setPreviewUrl] = useState<string | null>(null);
  const [isUploading, setIsUploading] = useState(false);
  const [uploadError, setUploadError] = useState<string | null>(null);
  const [snackbar, setSnackbar] = useState({
    open: false,
    message: "",
    severity: "info" as "success" | "error" | "warning" | "info",
  });

  const handleFile = useCallback(
    (file: File) => {
      if (file.size > 5 * 1024 * 1024) {
        setUploadError("  5MB  .");
        return;
      }

      if (file.type.startsWith("image/")) {
        setUploadedFile(file);
        const reader = new FileReader();
        reader.onloadend = () => {
          setPreviewUrl(reader.result as string);
        };
        reader.readAsDataURL(file);
      } else {
        setUploadError("    .");
      }
    },
    [setUploadError, setUploadedFile, setPreviewUrl]
  );

  const handleDragOver = useCallback((e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(true);
  }, []);

  const handleDragLeave = useCallback((e: React.DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    setIsDragging(false);
  }, []);

  const handleDrop = useCallback(
    (e: React.DragEvent<HTMLDivElement>) => {
      e.preventDefault();
      e.stopPropagation();
      setIsDragging(false);
      setUploadError(null);

      const files = e.dataTransfer.files;
      if (files && files.length > 0) {
        handleFile(files[0]);
      }
    },
    [handleFile, setUploadError]
  );

  const handleFileChange = useCallback(
    (e: React.ChangeEvent<HTMLInputElement>) => {
      setUploadError(null);
      const files = e.target.files;
      if (files && files.length > 0) {
        const file = files[0];
        handleFile(file);
      }
    },
    [handleFile, setUploadError]
  );

  const handleCloseSnackbar = () => {
    setSnackbar({ ...snackbar, open: false });
  };

  if (!user) return null;

  const handleLogout = async () => {
    onClose();
    await logout();
  };

  const handleOpenEditProfile = () => {
    setDisplayName(user.displayName);
    setPhotoURL(user.photoURL);
    setPreviewUrl(null);
    setUploadedFile(null);
    setUploadError(null);
    setEditProfileOpen(true);
    onClose();
  };

  const handleSaveProfile = async () => {
    try {
      setIsUploading(true);
      setUploadError(null);

      let finalPhotoURL = photoURL;

      if (uploadedFile && previewUrl) {
        finalPhotoURL = previewUrl;
      }

      await updateProfile(displayName, finalPhotoURL);
      setEditProfileOpen(false);

      setSnackbar({
        open: true,
        message: "  .",
        severity: "success",
      });
    } catch (error) {
      console.error("  :", error);
      setUploadError("    .");

      setSnackbar({
        open: true,
        message: "    .",
        severity: "error",
      });
    } finally {
      setIsUploading(false);
    }
  };

  const handleResetData = () => {
    setResetConfirmOpen(true);
    onClose();
  };

  const handleConfirmReset = () => {
    onClearData();
    setResetConfirmOpen(false);
  };

  return (
    <>
      <Menu
        anchorEl={anchorEl}
        open={Boolean(anchorEl)}
        onClose={onClose}
        PaperProps={{
          elevation: 3,
          sx: { width: 250, mt: 1 },
        }}
      >
        <Box sx={{ p: 2, textAlign: "center" }}>
          <Avatar
            src={user.photoURL}
            alt={user.displayName}
            sx={{ width: 60, height: 60, mx: "auto", mb: 1 }}
          />
          <Typography variant="subtitle1">{user.displayName}</Typography>
          <Typography variant="body2" color="text.secondary">
            {user.email}
          </Typography>
        </Box>

        <Divider />

        <MenuItem onClick={handleOpenEditProfile}>
          <ListItemIcon>
            <Edit fontSize="small" />
          </ListItemIcon>
          <ListItemText> </ListItemText>
        </MenuItem>

        <MenuItem onClick={handleResetData}>
          <ListItemIcon>
            <DeleteForever fontSize="small" />
          </ListItemIcon>
          <ListItemText>  </ListItemText>
        </MenuItem>

        <Divider />

        <MenuItem onClick={handleLogout}>
          <ListItemIcon>
            <Logout fontSize="small" />
          </ListItemIcon>
          <ListItemText></ListItemText>
        </MenuItem>
      </Menu>

      <Dialog
        open={editProfileOpen}
        onClose={() => !isUploading && setEditProfileOpen(false)}
        fullWidth
        maxWidth="xs"
      >
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              mt: 2,
            }}
          >
            <Paper
              sx={{
                width: 120,
                height: 120,
                borderRadius: "50%",
                position: "relative",
                mb: 2,
                overflow: "visible",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                border: isDragging
                  ? "2px dashed #00BFFF"
                  : "2px solid transparent",
                transition: "border 0.3s ease",
                bgcolor: "transparent",
              }}
              onDragOver={handleDragOver}
              onDragLeave={handleDragLeave}
              onDrop={handleDrop}
            >
              {isUploading ? (
                <CircularProgress size={40} />
              ) : (
                <>
                  <Avatar
                    src={previewUrl || photoURL}
                    alt={displayName}
                    sx={{ width: 120, height: 120, overflow: "hidden" }}
                  />
                  <input
                    type="file"
                    ref={fileInputRef}
                    onChange={handleFileChange}
                    accept="image/*"
                    style={{ display: "none" }}
                  />
                  <IconButton
                    aria-label="upload picture"
                    sx={{
                      position: "absolute",
                      bottom: -5,
                      right: -5,
                      backgroundColor: "rgba(0,0,0,0.6)",
                      "&:hover": { backgroundColor: "rgba(0,0,0,0.8)" },
                      zIndex: 1,
                      width: 32,
                      height: 32,
                      boxShadow: "0 0 5px rgba(0,0,0,0.5)",
                    }}
                    onClick={() => fileInputRef.current?.click()}
                    disabled={isUploading}
                  >
                    <AddAPhoto fontSize="small" />
                  </IconButton>
                </>
              )}
            </Paper>

            <Typography variant="caption" color="text.secondary" sx={{ mb: 2 }}>
                  
            </Typography>

            {uploadError && (
              <Typography variant="body2" color="error" sx={{ mb: 2 }}>
                {uploadError}
              </Typography>
            )}

            {uploadedFile && (
              <Typography
                variant="caption"
                color="text.secondary"
                sx={{ mb: 2 }}
              >
                 : {uploadedFile.name} (
                {(uploadedFile.size / 1024).toFixed(1)} KB)
              </Typography>
            )}

            <TextField
              label=""
              variant="outlined"
              fullWidth
              value={displayName}
              onChange={(e) => setDisplayName(e.target.value)}
              sx={{ mb: 2 }}
              disabled={isUploading}
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button
            onClick={() => setEditProfileOpen(false)}
            disabled={isUploading}
          >
            
          </Button>
          <Button
            onClick={handleSaveProfile}
            variant="contained"
            color="primary"
            disabled={isUploading}
          >
            {isUploading ? " ..." : ""}
          </Button>
        </DialogActions>
      </Dialog>

      <Dialog
        open={resetConfirmOpen}
        onClose={() => setResetConfirmOpen(false)}
      >
        <DialogTitle
          color="error"
          sx={{ display: "flex", alignItems: "center", gap: 1 }}
        >
          <Warning color="error" />
            
        </DialogTitle>
        <DialogContent>
          <Typography>
            ,  ,      
            .     . ?
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button onClick={() => setResetConfirmOpen(false)}></Button>
          <Button
            onClick={handleConfirmReset}
            variant="contained"
            color="error"
          >
            
          </Button>
        </DialogActions>
      </Dialog>

      <Snackbar
        open={snackbar.open}
        autoHideDuration={3000}
        onClose={handleCloseSnackbar}
        anchorOrigin={{ vertical: "bottom", horizontal: "center" }}
      >
        <Alert
          onClose={handleCloseSnackbar}
          severity={snackbar.severity}
          sx={{ width: "100%" }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </>
  );
};

export default ProfileMenu;
