import React, { useState } from "react";
import {
  Box,
  Typography,
  List,
  ListItemButton,
  ListItemText,
  IconButton,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Divider,
  ListItemSecondaryAction,
  DialogContentText,
} from "@mui/material";
import {
  Add as AddIcon,
  Delete as DeleteIcon,
  PlayArrow as PlayArrowIcon,
  Edit as EditIcon,
  QueueMusic as QueueMusicIcon,
} from "@mui/icons-material";
import { Playlist, Video } from "../types";

interface PlaylistsProps {
  playlists: Playlist[];
  onCreatePlaylist: (name: string) => void;
  onDeletePlaylist: (playlistId: string) => void;
  onRenamePlaylist: (playlistId: string, newName: string) => void;
  onPlayPlaylist: (playlistId: string) => void;
  onAddToQueue: (videos: Video[]) => void;
  onSelectPlaylist: (playlistId: string) => void;
  selectedPlaylistId: string | null;
}

const Playlists: React.FC<PlaylistsProps> = ({
  playlists,
  onCreatePlaylist,
  onDeletePlaylist,
  onRenamePlaylist,
  onPlayPlaylist,
  onAddToQueue,
  onSelectPlaylist,
  selectedPlaylistId,
}) => {
  const [createDialogOpen, setCreateDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [renameDialogOpen, setRenameDialogOpen] = useState(false);
  const [newPlaylistName, setNewPlaylistName] = useState("");
  const [selectedPlaylist, setSelectedPlaylist] = useState<Playlist | null>(
    null
  );

  const handleCreateOpen = () => {
    setNewPlaylistName("");
    setCreateDialogOpen(true);
  };

  const handleCreateClose = () => {
    setCreateDialogOpen(false);
  };

  const handleCreateSubmit = () => {
    if (newPlaylistName.trim()) {
      onCreatePlaylist(newPlaylistName.trim());
      setCreateDialogOpen(false);
      setNewPlaylistName("");
    }
  };

  const handleDeleteOpen = (playlist: Playlist) => {
    setSelectedPlaylist(playlist);
    setDeleteDialogOpen(true);
  };

  const handleDeleteClose = () => {
    setDeleteDialogOpen(false);
    setSelectedPlaylist(null);
  };

  const handleDeleteSubmit = () => {
    if (selectedPlaylist) {
      onDeletePlaylist(selectedPlaylist.id);
      setDeleteDialogOpen(false);
      setSelectedPlaylist(null);
    }
  };

  const handleRenameOpen = (playlist: Playlist) => {
    setSelectedPlaylist(playlist);
    setNewPlaylistName(playlist.name);
    setRenameDialogOpen(true);
  };

  const handleRenameClose = () => {
    setRenameDialogOpen(false);
    setSelectedPlaylist(null);
  };

  const handleRenameSubmit = () => {
    if (selectedPlaylist && newPlaylistName.trim()) {
      onRenamePlaylist(selectedPlaylist.id, newPlaylistName.trim());
      setRenameDialogOpen(false);
      setSelectedPlaylist(null);
    }
  };

  const formatDate = (timestamp: number) => {
    return new Date(timestamp).toLocaleDateString("ko-KR", {
      year: "numeric",
      month: "long",
      day: "numeric",
    });
  };

  return (
    <Box sx={{ width: "100%", height: "100%", overflow: "auto" }}>
      <Box
        sx={{
          p: 2,
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
        }}
      >
        <Typography variant="h6"> </Typography>
        <IconButton color="primary" onClick={handleCreateOpen}>
          <AddIcon />
        </IconButton>
      </Box>

      <Divider />

      <List sx={{ width: "100%" }}>
        {playlists.length === 0 ? (
          <Box sx={{ p: 2, textAlign: "center" }}>
            <Typography variant="body2" color="text.secondary">
                .   !
            </Typography>
          </Box>
        ) : (
          playlists.map((playlist) => (
            <ListItemButton
              key={playlist.id}
              selected={selectedPlaylistId === playlist.id}
              onClick={() => onSelectPlaylist(playlist.id)}
              sx={{
                borderLeft:
                  selectedPlaylistId === playlist.id
                    ? "4px solid #1DB954"
                    : "none",
                pl: selectedPlaylistId === playlist.id ? 2 : 3,
              }}
            >
              <ListItemText
                primary={playlist.name}
                secondary={`${playlist.videos.length}  ${formatDate(
                  playlist.createdAt
                )}`}
              />
              <ListItemSecondaryAction>
                <IconButton
                  edge="end"
                  onClick={() => onPlayPlaylist(playlist.id)}
                >
                  <PlayArrowIcon fontSize="small" />
                </IconButton>
                <IconButton
                  edge="end"
                  onClick={() => onAddToQueue(playlist.videos)}
                >
                  <QueueMusicIcon fontSize="small" />
                </IconButton>
                <IconButton
                  edge="end"
                  onClick={() => handleRenameOpen(playlist)}
                >
                  <EditIcon fontSize="small" />
                </IconButton>
                <IconButton
                  edge="end"
                  onClick={() => handleDeleteOpen(playlist)}
                >
                  <DeleteIcon fontSize="small" />
                </IconButton>
              </ListItemSecondaryAction>
            </ListItemButton>
          ))
        )}
      </List>

      {/*    */}
      <Dialog open={createDialogOpen} onClose={handleCreateClose}>
        <DialogTitle>  </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label=" "
            type="text"
            fullWidth
            value={newPlaylistName}
            onChange={(e) => setNewPlaylistName(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleCreateClose}></Button>
          <Button onClick={handleCreateSubmit} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*    */}
      <Dialog open={deleteDialogOpen} onClose={handleDeleteClose}>
        <DialogTitle> </DialogTitle>
        <DialogContent>
          <DialogContentText>
            '{selectedPlaylist?.name}'   ?
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDeleteClose}></Button>
          <Button onClick={handleDeleteSubmit} color="error">
            
          </Button>
        </DialogActions>
      </Dialog>

      {/*     */}
      <Dialog open={renameDialogOpen} onClose={handleRenameClose}>
        <DialogTitle>  </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            label=" "
            type="text"
            fullWidth
            value={newPlaylistName}
            onChange={(e) => setNewPlaylistName(e.target.value)}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={handleRenameClose}></Button>
          <Button onClick={handleRenameSubmit} color="primary">
            
          </Button>
        </DialogActions>
      </Dialog>
    </Box>
  );
};

export default Playlists;
